<template>
  <div class="col-md-12">
    <div class="col-lg-3 col-sm-6" v-for="(index, key) in stats" :key="key">
      <stats-card>
        <div 
          class="icon-big text-center icon-success" 
          slot="header"
        >
          <i :class="[index.icon]" />
        </div>
          <div class="numbers" slot="content">
            <p>{{index.name}}</p> {{getItemsStats[index.key]}}
          </div>
      </stats-card>
    </div>
  </div>
</template>

<script>
  import { mapGetters } from 'vuex'
  import { stats } from './stats.mock.js'

  export default {
    data: () => ({ stats }),
    components: {
      StatsCard: () => import('ui/Cards/StatsCard.vue')
    },
    computed: {
      ...mapGetters([
        'getItemsStats'
      ])
    }
  }
</script>

<style lang="">
    
</style>



